___CSS_LOADER_EXPORT___.push([module.id, `* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  font-weight: 100;
  font-style: normal;
}
.skip-link {
  position: absolute;
  top: -40px;
  left: 0;
  background-color: #bf1722;
  color: white;
  padding: 8px;
  z-index: 100;
}
 
.skip-link:focus {
   top: 0;
}

.header{
  display: inline-flex;
  width: 100%;
  text-align: center;
  margin-left: auto;
  margin-right: auto;
  top: 0;
  background-color: #212121;
  align-content: center;
  align-items: center;
  padding: 15px;
}
.name{
  width: 90%;
  font-size: 2em;
  font-weight: 900;
  margin: 0.2em 0.2em;
  color: white;
  display: inline-block;
}

.Menu {
  font-size: 31px;
  margin: 10px auto;
  display: block;
  width: 44px;
  color: black;
  
}

.nav {
  display: flex;
  background-color: #212121;
  padding: 20px 0;
  justify-content: space-between;
  align-items: center;
  position: fixed; 
  width: 100%;
  top: 0; 
  z-index: 1000; 
}

.nav_list {
  width: 100%;
  padding: 0;
  margin: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  left: -180px;
  gap: 10px;
}

.nav_list li {
  min-width: 100px;
  width: 15%;
  font-size: 20px;
  line-height: 44px;
  text-align: center;
  text-transform: uppercase;
  display: inline-block;
  font-weight: bold;
  margin: 20px;
}


.nav_list li a {
  text-decoration: none;
  color: white;
  padding:0 8px;
  white-space: nowrap;
  font-size: 1em; 
  display: inline-block; 
  min-width: 44px; 
  min-height: 44px;
}

.nav_list li a:hover {
  color: #34495e;
  font-size: larger;
}

.jumbotron {
  background-image: linear-gradient(rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0.8)), 
    url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  background-position: top;
  min-height: 800px;
  width: 100%;
  color: #F3EEEA;
  display: flex; 
  justify-content: center; 
  align-items: center; 
  font-size: 40px;
}

.tron__inner {
  text-align: center;
  margin: 0 auto;
  max-width: 800px;
}

.tron__title {
  margin-bottom: 10px;
  color: #ffffff;
  font-size: 1.2em;
}

.tron__tagline {
  margin-bottom: 20px;
}


main {
  width: 100%;
  margin: 0 auto;
}

.content {
  padding: 25px;
  text-align: left;
  overflow: auto;
}

.favorite{
  text-align: center;
  font-size: 38px;
  padding-top: 40px;
}

// Gaya untuk ditampilkan saat tidak ada restoran favorit
.no-favorite {
  .empty_favorite {
    display: block !important;
  }
}

.empty_favorite {
  display: none; 
  font-size: 18px;
  color: #666;
  text-align: center;
  margin-top: 20px;
}

.label {
  width: 100%;
  margin: 20px auto;
  text-align: center;
}
.explore_label {
  text-align: center;
  font-size: 38px;
  padding-top: 40px;
}

.contentlist {
  margin: 20px;
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.3);
  width: 95%;
  border-radius: 10px;
  overflow: hidden;
  background-color: #f5f5f5;
  transition: transform 0.3s ease-in-out;
}

.contentlist:hover {
  transform: scale(1.02);
}

.contentlist a {
  text-decoration: none;
  color: #2c3e50;
}

.image {
  max-height: 220px;
  width: 100%;
  border-top-right-radius: 10px;
  border-top-left-radius: 10px;
  object-fit: cover;
}

.restaurant_info {
  padding: 24px;
  color: #34495e;
  font-weight: 400;
}

.info_title:hover {
  text-decoration: underline;
}

.info_city {
  margin-top: 5px;
  color: #7f8c8d;
}

.info_desc {
  margin-top: 10px;
  text-align: justify;
  line-height: 1.5;
  font-weight: 400;
}

.info_rates {
  color: #ffffff;
  text-align: center;
  margin-top: 10px;
  padding: 8px;
  font-weight: bold;
  border-radius: 20px;
  background-color: #34495e;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
}

.info_rates:hover{
  background-color: #C9D6DF;
  color: #34495e;
}


.detail {
  width: 100%;
  margin: 60px auto;
  text-align: center;
}


.detail_head {
  text-align: center;
  color: #34495e;
  font-size: 32px;
  font-weight: bold;
  text-transform: uppercase;
  letter-spacing: 2px;
}


.detail_content {
  padding: 25px;
  overflow: auto;
  background-color: #52616B;
  border-radius: 10px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  margin-bottom: 50px;
}

.contentdetail {
  margin: auto;
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.3);
  width: 80%;
  border-radius: 15px;
  overflow: hidden;
  background-color: #C9D6DF;
}

.detail_title {
  font-weight: 800;
  margin-bottom: 20px;
  padding-top: 20px;
  text-align: center;
  text-transform: uppercase;
  letter-spacing: 1px;
  color: #333333;
  font-size: 20px;
}

.info_list {
  text-align: left;
  margin: 20px auto auto;
  padding: 30px;
  border: 2px solid #52616B;
  border-radius: 10px;
  background-color: #f9f9f9;
  max-width: 100%;
  box-sizing: border-box;
  transition: transform 0.3s, box-shadow 0.3s;
}

.info_list:hover{
  transform: translateY(-5px);
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}


.foods_detail,
.drinks_detail {
  display: flex;
  flex-direction: column;
  text-align: center;
  align-content: center;
  margin-bottom: 30px;
  font-weight: 400;
}

.restaurant_info_detail {
  padding: 25px;
  color: #34495e;
  font-weight: 400;
}

.restaurant_menu {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(min(200px, 100%), 1fr));
  gap: 30px;
  margin: 30px auto;
  padding: 20px;
  border: 2px solid #52616B;
  border-radius: 20px;
  background-color: #f9f9f9;
  text-align: center;
  transition: transform 0.3s, box-shadow 0.3s;
}

.restaurant_menu:hover{
  transform: translateY(-5px);
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

.image_detail {
  width: 100%;
  max-height: 500px;
  border-radius: 10px;
  object-fit: cover;
}

.list {
  margin-bottom: 12px;
  font-size: 18px;
  color: #2c3e50;
}

.restaurant_review {
  text-align: center;
  margin-top: 40px;
}

.review_header {
  margin-top: 15px;
  font-size: 24px;
  font-weight: bold;
  color: #34495e;
}

.review_item {
  grid-template-columns: repeat(auto-fit, minmax(min(200px, 100%), 1fr));
  gap: 30px;
  margin: 30px auto;
  padding: 20px;
  border: 2px solid #52616B;
  border-radius: 20px;
  background-color: #f9f9f9;
  text-align: left;
  transition: transform 0.3s, box-shadow 0.3s;
}

.review_item:hover{
  transform: translateY(-5px);
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

ul {
  list-style-type: none;
  padding: 0;
}


.like {
  width: 55px;
  height: 55px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #C9D6DF;

  position: fixed;
  bottom: 16px;
  right: 16px;
  border-radius: 50%;
  border: 0;

  font-size: 18px;
  color: #52616B;
  cursor: pointer;
}

.like:hover {
  background-color: #1E2022;
}

.review_header {
  margin-bottom: 5px;
}

.footer {
  background-color: #212121;
  color: #fff;
  padding: 2em;
  text-align: center;
  width: 100%;
  margin-top: 50px;
  font-size: 23px;
}

@media  screen and ( max-width: 1050px){
    .name{
      font-size: 26px;
    }
    .nav_list{
      width: 30%;
      font-size: 13px;
    }
 }
  
@media  screen and ( max-width: 499px){
  .header{
    display: grid;
    grid-template-columns: 1fr 1fr;
    padding: 12px 25px;
    margin-top: 0;
  }
  .nav-mob{
    display: flex;
    align-items: center;
    justify-content: flex-start;
    margin-top: 15px;
    list-style-type: none;
  }

  .name{
    display: flex;
    justify-content: flex-end;
    align-items: center;
    font-size: 24px;
  }
  #hamburger{
    line-height: 44px;
    margin-top: 0px;
    color:black;
    text-decoration: none;
    background-color: transparent;
    border: none;
  }
  .nav {
    z-index: 10;
    background-color: #EBE3D5;
    width: 150px;
    position: absolute;
    padding: 8px;
    -webkit-transform: translate(-300px, 0);
    transform: translate(-300px, 0);
    transition: transform 0.3s ease;
    top: 92px;
    overflow-x: hidden;
    left: -300px;
  }
  .nav_list{
    display: list-item;
    border-bottom: 1px solid #EBE3D5;
    width: 100%;
    text-align: left;
    flex-direction: column;
    align-items: flex-start;
  }
  .open {
    -webkit-transform: translate(0, 0);
    transform: translate(0, 0);
    left: 0;
  }
  

}
@media screen and (min-width: 500px){
  .Menu{
    display: none;
  }
}

@media screen and (min-width: 670px) {
  .header{
    grid-template-columns: 1fr auto;
    padding: 8px 32px;
  }
  .nav{
    position: static;
    width: 100%;
  }
  .maincontent {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-column-gap: 10px;
    grid-row-gap: 15px;
  }
  .Menu{
    display: none;
  }
}

@media screen and (min-width: 950px) {
  .content {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
  }
}
@media screen and (min-width: 1200px) {
  main {
    max-width: 1200px;
  }
  .Menu{
    display: none;
  }
  
}
@media (max-width: 1200px) {
  .jumbotron {
    background-image: linear-gradient(rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0.8)), 
      url(${___CSS_LOADER_URL_REPLACEMENT_1___});
  }
}
@media (max-width: 1024px) {
  .jumbotron {
    background-image: linear-gradient(rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0.8)), 
      url(${___CSS_LOADER_URL_REPLACEMENT_2___});
  }
}
@media (max-width: 768px) {
  .jumbotron {
    background-image: linear-gradient(rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0.8)), 
      url(${___CSS_LOADER_URL_REPLACEMENT_3___});
  }
}


 
